import React, { useState } from 'react';
import {
  Nav,
  NavBox,
  NavList,
  NavListItem,
  NavListItemLink,
} from './Navigation.styled';

export const Navigation = () => {
  const [, setScrollX] = useState(0);//scrollX

  const handleSliderScroll = e => {
    const container = e.target;
    const scrollLeft = container.scrollLeft;
    setScrollX(scrollLeft);
  };

  return (
    <NavBox>
      <Nav onScroll={handleSliderScroll}>
        <NavList>
          <NavListItem>
            <NavListItemLink href=""></NavListItemLink>
          </NavListItem>

          <NavListItem>
            <NavListItemLink href=""></NavListItemLink>
          </NavListItem>

          <NavListItem>
            <NavListItemLink href=""> </NavListItemLink>
          </NavListItem>

          <NavListItem>
            <NavListItemLink href="">   </NavListItemLink>
          </NavListItem>

          <NavListItem>
            <NavListItemLink href="">, , </NavListItemLink>
          </NavListItem>
          <NavListItem>
            <NavListItemLink href=""></NavListItemLink>
          </NavListItem>

          <NavListItem>
            <NavListItemLink href="">   </NavListItemLink>
          </NavListItem>
        </NavList>
      </Nav>
    </NavBox>
  );
};
